.page {
  scroll-behavior: smooth;

  &:has(.page__menu:target) {
    overflow: hidden;
  }

  &__body {
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    pointer-events: none;
    transform: translateX(-100%);
    transition: all 300ms;

    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0);
    }
  }
}