@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin contentPaddingInline() {
  padding-inline: 20px;

  @include onTablet() {
    padding-inline: 40px;
  }

  @include onDesktop() {
    max-width: 1170px;
    margin-inline: auto;
    padding-inline: 55px;
  }
}

@mixin pageGrid {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet() {
    --columns: 6;
    column-gap: 30px;
  }

  @include onDesktop() {
    --columns: 12;
  }
}

@mixin link-hover {
  &:hover {
    color: $link-hover-color;
  }
}